/* 头像旋转 */
.home .home-profile .home-avatar img {
  width: 9rem;

/* 设置循环动画
[animation: 
(play)动画名称
(2s)动画播放时长单位秒或微秒
(ease-out)动画播放的速度曲线为以低速结束 
(1s)等待1秒然后开始动画
(1)动画播放次数(infinite为循环播放) ]*/

/* 鼠标经过头像旋转360度 */
-webkit-transition: -webkit-transform 1.0s ease-out;
-moz-transition: -moz-transform 1.0s ease-out;
transition: transform 1.0s ease-out;
  &:hover {
    /* 鼠标经过停止头像旋转 
    -webkit-animation-play-state:paused;
    animation-play-state:paused;*/

    /* 鼠标经过头像旋转360度 */
    -webkit-transform: rotateZ(360deg);
    -moz-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
  }
}
/* Z 轴旋转动画 */
@-webkit-keyframes play {
0% {
  -webkit-transform: rotateZ(0deg);
}
100% {
  -webkit-transform: rotateZ(-360deg);
}
}
@-moz-keyframes play {
0% {
  -moz-transform: rotateZ(0deg);
}
100% {
  -moz-transform: rotateZ(-360deg);
}
}
@keyframes play {
0% {
  transform: rotateZ(0deg);
}
100% {
  transform: rotateZ(-360deg);
}
}





/************************** 评论插件textrea背景特效 **************************/
#wl-edit {
  background-image: url(/images/common/comment-bg.gif) !important;
  background-size: auto 100%;
  background-repeat: no-repeat;background-position: right;
}
textarea#wl-edit{
  background-image: url(/images/common/comment-bg.gif) !important;
  background-size: auto 100%;
  background-repeat: no-repeat;background-position: right;
  transition: all 0.5s ease-in-out 0s;
}
textarea#wl-edit:focus {
  background-position-x: -150px;
  transition: all 0.5s ease-in-out 0s;
}
/************************** 评论插件textrea背景特效 **************************/


/************************** div 滚动条样式设定 **************************/
div{
  scrollbar-width: none; /*火狐隐藏滚动条*/
  /* scrollbar-color: transparent transparent;*/
  /* scrollbar-track-color: transparent;
  -ms-scrollbar-track-color: transparent; */
  /* 上面注释的是让滚动条变透明的 */
}
/** div 滚动条样式设定 */
div::-webkit-scrollbar {  /* 仅对webkit内核浏览器生效如 chrome edge */
  width: 1px; /* 高宽分别对应横竖滚动条的尺寸,1px为了方便查看，如果不想显示填0 */
  height: 1px; /* 高度参数没有作用可不写 */
  /*display: none;*/ /*隐藏滚动条*/
}
/************************** div 滚动条样式设定 **************************/

