/** 自定义鼠标样式 */
html {
    cursor: url(/cursor/default.png), auto;
  }
  button, a, input[type="button"], input[type="submit"] {
    cursor: url('/cursor/pointer.png'), pointer;
  }
  /* 链接 */
  a, a:link, a:visited {
    cursor: url('/cursor/pointer.png'), pointer;
  }
  p {
    cursor: url(/cursor/text.png), auto;
  }
  textarea {
    cursor: url('/cursor/text.png'), text;
  }
  
  input[type="text"], input[type="email"], input[type="password"], input[type="number"] {
    cursor: url('/cursor/text.png'), text;
  }
  
  input[type="checkbox"], input[type="radio"] {
    cursor: url('/cursor/default.png'), default;
  }
  /* 选择文本 */
  ::selection {
    cursor: url('/cursor/text.png'), text;
  }
  
  /* 禁止型 */
  input[disabled], button[disabled] {
    cursor: url('/cursor/not-allowed.png'), not-allowed;
  }
  
  /* 可拖动对象 */
  .draggable {
    cursor: url('/cursor/grab.png'), grab;
  }
  
  /* 拖动中 */
  .dragging {
    cursor: grabbing;
  }
  
  ::-webkit-scrollbar-track { /*滚动条里面轨道*/
    cursor:  url('/cursor/pointer.png'), auto;
  }
  
  ::-webkit-scrollbar-thumb { /*滚动条里面滑块*/
    cursor: url('/cursor/grab.png'), auto;
  }
  